body {
  font-family: $primeryFont;
  color: $primeryTextColor;
  background-color: $primeryBgColor;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  @extend %listReset;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
